@import url(https://fonts.googleapis.com/css2?family=Kaisei+Tokumin:wght@400;500;700;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;600&display=swap);
*,body,h1,h2,h3,div,section,p,ul,li{box-sizing:border-box;margin:0;padding:0}ul,li{list-style:none}a{color:inherit;text-decoration:none}address{font-style:normal}html{font-family:"Open Sans", sans-serif;font-size:11.6px;font-weight:300;scroll-behavior:smooth}body{width:100vw;overflow-x:hidden}h1,h2,h3,h4{font-family:"Kaisei Tokumin", serif}#container{width:84vw;max-width:1120px;margin:0 auto}.hover-button{background:linear-gradient(90deg, transparent calc(100% / 3), currentColor 0 calc(300% / 3), transparent 0) 100% 100%/200% 1px repeat-x;background:linear-gradient(90deg, transparent calc(100% / 3), currentColor 0 calc(300% / 3), transparent 0) var(--d, 100%) 100%/200% 1px repeat-x;cursor:pointer}.hover-button:hover{--d: -100%;transition:0.7s}.loading{width:100vw;height:100vh;background:black;position:fixed;top:0;left:0;z-index:99}.loading.false{-webkit-transform:translateY(-100%);transform:translateY(-100%);transition:0.6s 0.5s}.loading .loading__title{width:84vw;max-width:1120px;margin:0 auto;padding:30px 0 10px;display:flex;align-items:center;position:relative;top:65vh;opacity:1}.loading .loading__title .loading__title__icon{width:18px;height:18px;background:white;border-radius:50%;margin-right:10px;-webkit-animation:blink 1s 1s infinite;animation:blink 1s 1s infinite}.loading .loading__title .loading__title__text{font-family:"Open Sans", sans-serif;font-size:1rem;color:white;font-weight:400;line-height:1.2}@-webkit-keyframes blink{0%{background:white}30%{background:black}60%{background:white}}@keyframes blink{0%{background:white}30%{background:black}60%{background:white}}.loading.false .loading__title{opacity:0;transition:1s}

